import { Component, OnInit, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { AppService } from '../services/app.service';
import { HttpService } from '../services/http.service';
import { LoginService } from '../services/login.service';
import { ToStr } from '../pipes/round_str.pipe';
import * as jsPDF from 'jspdf';
import 'jspdf-autotable';

@Component({
  selector: 'app-transactions',
  templateUrl: './transactions.component.html',
  styleUrls: ['./transactions.component.css']
})
export class TransactionsComponent implements OnInit {
  id;
  constructor(private tostr: ToStr,private ref: ChangeDetectorRef, private appService: AppService, private httpService: HttpService,
    private login: LoginService) {


  }
  txtFilter;
  messageTransaction;

  //filter

  addFilter(str) {
    this.txtFilter = str;
    console.log(str);
  }
  //sort
  boolSort = false;
  sortTable(cmpName) {
    this.boolSort = !this.boolSort;
    if (this.boolSort) {
      this.messageTransaction.sort(this.predicateBy(cmpName));
    }
    else {
      this.messageTransaction.sort(this.predicateByRev(cmpName));
    }
  }

  predicateBy(prop) {
    return function (a, b) {
      var x, y;
      if (typeof a[prop] === 'string') {
        x = a[prop].toLowerCase();
        y = b[prop].toLowerCase();
      }
      else {
        x = a[prop];
        y = b[prop];
      }
      if (x > y) {
        return 1;
      } else if (x < y) {
        return -1;
      }
      return 0;
    }
  }

  predicateByRev(prop) {
    return function (a, b) {
      var x, y;
      if (typeof a[prop] === 'string') {
        x = a[prop].toLowerCase();
        y = b[prop].toLowerCase();
      }
      else {
        x = a[prop];
        y = b[prop];
      }
      if (x > y) {
        return -1;
      } else if (x < y) {
        return 1;
      }
      return 0;
    }
  }

  //excell file

  toExcel() {
    var arrData = this.message;
    var CSV = '';

    //This condition will generate the Label/Header
    var row = "";
    //This loop will extract the label from 1st index of on array
    for (var index in arrData[0]) {
      //Now convert each value to string and comma-seprated
      row += index + ',';
    }
    row = row.slice(0, -1);
    //append Label row with line break
    CSV += row + '\r\n';


    //1st loop is to extract each row
    for (var i = 0; i < arrData.length; i++) {
      row = "";
      //2nd loop will extract each column and convert it in string comma-seprated
      for (var index in arrData[i]) {
        if (typeof (arrData[i][index]) === 'number') {
          arrData[i][index] = Math.round(arrData[i][index]);
        }
        row += '"' + arrData[i][index] + '",';
      }
      row.slice(0, row.length - 1);
      //add a line break after each row
      CSV += row + '\r\n';
    }

    var fileName = "Equity";
    var uri = 'data:text/csv;charset=utf-8,' + escape(CSV);
    var link = document.createElement("a");
    link.href = uri;
    link.download = fileName + ".csv";

    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  //PDF
  toPDF() {
    var columns = [];
    var rows = [];
    for (var index in this.message[0]) {
      //Now convert each value to string and comma-seprated
      //if(index[' !=="SecurityCode")
      columns.push(index);
    }
    this.message.forEach(item => {
      var arr = Object.values(item);
      for (var x in arr) {
        if (typeof (arr[x]) === 'number') {
          arr[x] = this.round(arr[x]);

        }
      }
      rows.push(arr);
    })


    var doc = new jsPDF('landscape');
    doc.autoTable(columns, rows, {
      styles: {
        overflow: 'linebreak',
        halign: 'center',
        fontSize: 8
      }
    });
    doc.save('table.pdf');
  }
  round(num) {
    return this.tostr.transform(num);
  }
  message:any;
  ngOnInit() {
    this.appService.initialiseJQuery();
    this.appService.initChartDashBoard();

    this.httpService.getData<any>(this.login.domainUrl + this.login.domainService + 'GetEquityTransactions/' + this.login.userData.UserId + '/' + this.login.portfolioId + '/' + this.appService.securityCode).subscribe(data => {

      this.messageTransaction = data.Message;



    });
    this.httpService.getData<any>(this.login.domainUrl + this.login.domainService + 'GetEquityTransactions/' + this.login.userData.UserId + '/' + this.login.portfolioId + '/' + this.appService.securityCode).subscribe(data => {

      
      data.Message.forEach(item => {
        delete item['securitycode'];
        delete item['PortfolioId'];
        delete item['transactionnumber'];
        //item.companyname = item.companyname.trim();
        
      })
      this.message = data.Message;
      console.log(this.message);
    });



    
  }
}

